.button-effect {
	--fill-clr: hsl(var(--light-clr));
	--text-clr: hsl(var(--dark-clr));
	--dir: -100%;
	position: relative;
	isolation: isolate;
	overflow: hidden;
}

.button-effect:hover {
	color: var(--text-clr);
}

.button-effect:hover::after {
	top: 50%;
	-webkit-transform: translate(-50%, -50%);
	transform: translate(-50%, -50%);
}

.button-effect::after {
	content: "";
	position: absolute;
	top: 0;
	left: 50%;
	-webkit-transform: translate(-50%, var(--dir));
	transform: translate(-50%, var(--dir));
	width: 150%;
	height: 200%;
	border-radius: 50%;
	-webkit-transition: .3s ease-in;
	transition: .3s ease-in;
	background-color: var(--fill-clr);
	z-index: -1;
}

.button-effect--secondary {
	--fill-clr: hsl(var(--secondary-clr));
	--text-clr: hsl(var(--light-clr));
}

.button-effect--primary {
	--fill-clr: hsl(var(--primary-clr));
	--text-clr: hsl(var(--light-clr));
}

.button-effect--dark {
	--fill-clr: hsl(var(--dark-clr));
	--text-clr: hsl(var(--light-clr));
}

.button-effect--bottom {
	--dir: 100%;
}

