.featured-nav__link {
	display: inline-block;
	padding: .5rem 1rem .5rem 0;
	position: relative;
	isolation: isolate;
}

.featured-nav__link:not(:first-child) {
	-webkit-padding-start: 1rem;
	padding-inline-start: 1rem;
}

.featured-nav__link.active {
	color: hsl(var(--secondary-clr));
}

.featured-nav__link.active::after {
	content: "";
	position: absolute;
	inset-block-start: 100%;
	inset-inline: 0;
	height: 2px;
	background-color: hsl(var(--secondary-clr));
}

.featured-nav__link.active .featured-nav__link-count {
	color: hsl(var(--clr-one));
}

.featured-nav__link-count {
	display: inline-block;
	position: absolute;
	inset-inline-end: 0;
	inset-block-start: 0;
	font-size: 12px;
}

